.App {
background: orange;
padding: 20px;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

body {
    margin: 0;
  }
  
  html {
    transition: background-color 1s;
  }
  #root{
    width:80%;
    margin: 0 10%;
  }
  
  .quotebox {
    height: auto;
    width: 100%;
    border: 1px rgb(145, 126, 126) solid;
    border-radius: 10px;
    text-align: right;
    max-width: 35rem
    
  }
  
  #quote {
    margin: 20px;
    text-align: left;
  }
  #newquote{
      background: #191a19;
      outline: none;
      border: none;
  }
  
  .fadeIn {
    opacity: 0;
    display: block;
    animation: slidein 1s ease-out forwards;
  }
  
  @keyframes slidein {
    0% {
      opacity: 0;
      transform: translateY(-25px);
    }
    100% {
      opacity: 1;
      transform: translateY(0px);
    }
  }
  
  #author {
    margin: 20px;
    font-style: italic;
  }
  
  #newquote {
    margin: 20px;
    position: relative;
    border-radius: 5px;
    height: 35px;
    width: 100px;
    font-weight: 700;
    font-family: "Segoe UI", "Tahoma", "Geneva", "Verdana", sans-serif;
    color: white;
  }